import React from "react";
import { useRecoilState, useRecoilValue } from "recoil";
import { Categories, categoryState, customCategoryState, toDoSelector } from "../atoms";
import CreateCategory from "./CreateCategory";
import CreateToDos from "./CreateToDos";
import ToDo from "./ToDo";

function ToDoList() {
  const toDos = useRecoilValue(toDoSelector);
  const [category, setCategory] = useRecoilState(categoryState);
  const onInput = (event: React.FormEvent<HTMLSelectElement>) => {
    setCategory(event.currentTarget.value as any);
  };
  const customCategories = useRecoilValue(customCategoryState);
  return (
    <div>
      <h1>To Dos</h1>
      <hr />

      <h4>You can add your own category! :</h4>
      <CreateCategory/>

      <select value={category} onInput={onInput}>
        <option value={Categories.TO_DO}>To Do</option>
        <option value={Categories.ONGOING}>Doing</option>
        <option value={Categories.DONE}>Done</option>

        { customCategories.length !== 0? customCategories.map((category) => (
            <option value={category.category}>{category.category}</option>
            )): null
        }   
      </select>
      <CreateToDos />
      {toDos?.map((toDo) => (
        <ToDo key={toDo.id} {...toDo} />
      ))}
    </div>
  );
}

export default ToDoList;